import { cssProps } from '@oracle/oraclejet-cssprops';
import { mergeInterpolations } from '../utils/UNSAFE_mergeInterpolations';

type StylePropsWithOutTruncate = {
  /**
   * Specifies the color of the text.
   */
  variant?: 'primary' | 'secondary' | 'disabled' | 'danger' | 'warning' | 'success' | 'inherit';

  /**
   * Specifies the text "style". Sets font size and line height.
   */
  size?: '2xs' | 'xs' | 'sm' | 'md' | 'lg' | 'xl' | 'inherit';

  /**
   * Specifies the font weight.
   */
  weight?: 'normal' | 'semiBold' | 'bold' | 'inherit';

  /**
   * Specifies if hyphens should be included when handling long words with no spaces.
   */
  hyphens?: 'auto' | 'none';
};
type TruncateProps =
  | { lineClamp?: number; truncation?: never }
  | { lineClamp?: never; truncation?: 'none' | 'clip' | 'ellipsis' };
type AdditionalProps = {
  /**
   * Supports String
   */
  children?: string;
};
type Props = StylePropsWithOutTruncate & TruncateProps & AdditionalProps;

const styles = {
  base: cssProps`${{
    overflowX: 'hidden',
    overflowY: 'hidden',
    overflowWrap: 'anywhere',
    display: 'inline'
  }}`,
  lineClamp: cssProps`${{
    display: '-webkit-box',
    WebkitBoxOrient: 'vertical'
  }}`,
  truncation: {
    none: '',
    clip: cssProps`${{
      textOverflow: 'clip',
      whiteSpace: 'nowrap'
    }}`,
    ellipsis: cssProps`${{
      textOverflow: 'ellipsis',
      whiteSpace: 'nowrap',
      width: '100%',
      display: 'inline-block'
    }}`
  },
  variant: {
    primary: cssProps`${{
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-primary)'
    }}`,
    secondary: cssProps`${{
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-secondary)'
    }}`,
    disabled: cssProps`${{
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-disabled)'
    }}`,
    danger: cssProps`${{
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-danger-3)'
    }}`,
    warning: cssProps`${{
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-warning-3)'
    }}`,
    success: cssProps`${{
      color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-success-3)'
    }}`,
    inherit: cssProps`${{
      color: 'inherit'
    }}`
  },
  size: {
    '2xs': cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-2xs-font-size)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-2xs-line-height)'
    }}`,
    xs: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-xs-font-size)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-xs-line-height)'
    }}`,
    sm: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-sm-font-size)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-sm-line-height)'
    }}`,
    md: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-font-size)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-md-line-height)'
    }}`,
    lg: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-lg-font-size)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-lg-line-height)'
    }}`,
    xl: cssProps`${{
      fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-xl-font-size)',
      lineHeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-typography-body-xl-line-height)'
    }}`,
    inherit: cssProps`${{
      fontSize: 'inherit',
      lineHeight: 'inherit'
    }}`
  },
  weight: {
    normal: cssProps`${{
      fontWeight: 'normal'
    }}`,
    semiBold: cssProps`${{
      fontWeight: '600'
    }}`,
    bold: cssProps`${{
      fontWeight: '700'
    }}`,
    inherit: cssProps`${{
      fontWeight: 'inherit'
    }}`
  },
  hyphens: {
    auto: cssProps`${{
      hyphens: 'auto'
    }}`,
    none: cssProps`${{
      hyphens: 'none'
    }}`
  }
};
const interpolations = [
  () => ({ class: styles.base }),
  ({ size = 'inherit' }: Pick<StylePropsWithOutTruncate, 'size'>) => ({
    class: styles.size[size]
  }),
  ({ weight = 'inherit' }: Pick<StylePropsWithOutTruncate, 'weight'>) => ({
    class: styles.weight[weight]
  }),
  ({ variant = 'inherit' }: Pick<StylePropsWithOutTruncate, 'variant'>) => ({
    class: styles.variant[variant]
  }),
  ({ hyphens = 'auto' }: Pick<StylePropsWithOutTruncate, 'hyphens'>) => ({
    class: styles.hyphens[hyphens]
  }),
  ({ truncation = 'none' }: Pick<TruncateProps, 'truncation'>) => ({
    class: styles.truncation[truncation]
  }),
  ({ lineClamp }: Pick<TruncateProps, 'lineClamp'>) =>
    !lineClamp ? {} : { class: styles.lineClamp, webkitLineClamp: lineClamp }
];
const styleInterpolations = mergeInterpolations<Omit<Props, 'children'>>(interpolations);

export function Text({ children, ...props }: Props) {
  const { class: cls, ...styles } = styleInterpolations(props);
  return (
    <span class={cls} style={styles}>
      {children}
    </span>
  );
}
